import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GeneralReturnType } from '@app/class/general';

@Injectable({
  providedIn: 'root'
})
export class BannerImageService {


  constructor(
    private http: HttpClient
  ) { }

  getBannerImage(page: number = 0, limited: number = 0): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/banner`);
  }

  getActiveBannerImage(page: number = 0, limited: number = 0): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/active_banners`);
  }

  uploadBannerImage(data: any): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/banner', data);
  }

  toggleBannerVisibility(id): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/banner/${id}`, null);
  }

  deleteBannerImage(id: number): Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/banner/${id}`);
  }

}
