import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GeneralReturnType } from '@app/class/general';


@Injectable({
  providedIn: 'root'
})
export class RolesService {

  constructor(
    private http: HttpClient
  ) { }

  getRoles(): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>('/api/roles');
  }

  getRole(id: number): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/roles/${id}`);
  }

  deleteRole(id: number): Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/roles/${id}`);
  }

  createRoles(data: object): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/roles',  data);
  }
}
