import { Component, OnInit } from '@angular/core';
import { BannerImageService } from '@app/services/banner-image.service';
import { Notification } from '@app/utils/notification';

@Component({
  selector: 'app-admin-banners-page',
  templateUrl: './admin-banners-page.component.html',
  styleUrls: ['./admin-banners-page.component.css']
})
export class AdminBannersPageComponent implements OnInit {

  isUploading = false;
  constructor(
    private bannerImageServices: BannerImageService
  ) { }

  bannerImages: any;

  ngOnInit() {
    this.getBannerImages();
  }
  async getBannerImages() {
    this.bannerImageServices.getBannerImage()
      .subscribe(data => {this.bannerImages = data; } );
  }

  uploadImageInput(event: any) {
    this.isUploading = true;
    const reader = new FileReader();
    const file = event.target.files[0];
    let ext = file.name.split('.');
    ext = ext[ext.length - 1];

    reader.onloadend = () => {
        const img = reader.result.toString().split(',')[1];
        const uploadData = {
            type: ext,
            img
        };

        this.bannerImageServices.uploadBannerImage(uploadData)
        .subscribe(data => {
          Notification.show(data.message, data.status);
          this.isUploading = false;
          this.getBannerImages();
        });
    };
    reader.readAsDataURL(file);
  }

  deleteBannerImage(bannerImage: number) {
    this.bannerImageServices.deleteBannerImage(bannerImage)
      .subscribe( data => {
        Notification.show(data.message, data.status);
        this.getBannerImages();
      });
  }

  toggleBannerVisiblity(productImage: number) {
    this.bannerImageServices.toggleBannerVisibility(productImage)
      .subscribe( data => {
        Notification.show(data.message, data.status);
        this.getBannerImages();
      });
  }


}
