import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { ProductService } from '@app/services/product.service';
import { Notification } from '@app/utils/notification';
import { CategoriesService } from '@app/services/categories.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-product-categories',
  templateUrl: './product-categories.component.html',
  styleUrls: ['./product-categories.component.css']
})
export class ProductCategoriesComponent implements OnInit {
  @Input() product: any;
  @Output() refreshProduct = new EventEmitter();

  categories: any;
  category = '';


  constructor(
    private productService: ProductService,
    private categoriesService: CategoriesService
  ) { }

  ngOnInit() {
    this.getUnassociatedCategory();
  }

  onSubmit(form: NgForm) {
    this.productService.addProductCategory(this.product.id, form.form.value)
      .subscribe(data => {
        Notification.show(data.message, data.status);
        this.refreshProduct.emit(null);
        this.getUnassociatedCategory();
      });
  }

  deleteProductCategory(category: number) {
    this.productService.deleteProductCategory(this.product.id, category)
      .subscribe(
        data => {
          Notification.show(data.message, data.status);
          this.refreshProduct.emit(null);
          this.getUnassociatedCategory();
        }
      );
  }

  addProductCategory(category: number) {

  }

  makeProductMain(category: number) {
    this.productService.updateProducts(this.product.id, {category_id: category})
      .subscribe(
        data => {
          Notification.show(data.message, data.status);
          if (data.status === 'success') {
            this.refreshProduct.emit(null);
          }
        }
      );
  }

  getUnassociatedCategory() {
    this.categoriesService.getUnassociatedCategories(this.product.id)
      .subscribe(
        data => {
          this.categories = data;
          if (this.categories.data) {
            const optGroup = {};
            this.categories.data.forEach((category) => {
              const title = category.parent ? category.parent.title : 'Base Category';
              if (!optGroup[title]) {
                optGroup[title] = [];
              }
              optGroup[title].push(category);
            });
            this.categories.data = optGroup;
          }
        }
      );
  }

  asIs() { return 0; }
}
