import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { CategoriesService } from '@app/services/categories.service';

@Component({
  selector: 'app-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  styleUrls: ['./breadcrumb.component.css']
})
export class BreadcrumbComponent implements OnInit, OnChanges {
  @Input() data: any;
  @Input() product = false;
  breadcrumb = [];

  constructor(
    private categoryService: CategoriesService
  ) { }

  ngOnInit() {
    this.setBreadCrumb();
  }

  setBreadCrumb() {
    const id = this.product ? this.data.category.id : this.data.id;
    this.breadcrumb = this.getCategory(id);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (!changes.data.firstChange) {
      this.data = changes.data.currentValue;
      this.setBreadCrumb();
    }
  }

  getCategory(id: number, position = 0, breadcrumb = []): Array<any> {
    this.categoryService.getCategory(id)
      .subscribe(data => {
        if (data.status === 'success') {
          data.data.position = position;
          breadcrumb.unshift(data.data);
          if (data.data.parent) {
            position = (position || 0) + 1;
            return this.getCategory(data.data.parent.id, position, breadcrumb);
          }
        }
        return breadcrumb;
      }
    );
    return breadcrumb;
  }

}
