import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { AdminService } from '@app/services/admin.service';

@Component({
  selector: 'app-admin-staff-page',
  templateUrl: './admin-staff-page.component.html',
  styleUrls: ['./admin-staff-page.component.css']
})
export class AdminStaffPageComponent implements OnInit {

  params;

  admin;

  constructor(
    private route: ActivatedRoute,
    private adminService: AdminService
  ) { }

  ngOnInit() {
    this.route.params.subscribe(params => this.params = params);
    this.getAdministrator();
  }

  getAdministrator() {
    this.adminService.getAdministrator(this.params.id)
      .subscribe(data => this.admin = data);
  }

}
