import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '@app/services/user.service';
import { ProductService } from '@app/services/product.service';
import { ClientService } from '@app/services/client.service';
import { Notification } from '@app/utils/notification';


@Component({
  selector: 'app-client-layout',
  templateUrl: './client-layout.component.html',
  styleUrls: ['./client-layout.component.css'],
})
export class ClientLayoutComponent implements OnInit {
  client: any;
  isUploading = false;

  currentRoute: any;
  constructor(
    private router: Router,
    private userServices: UserService,
    private clientServices: ClientService
  ) { }

  uploadImageInput(event: any) {
    this.isUploading = true;
    const reader = new FileReader();
    const file = event.target.files[0];
    let ext = file.name.split('.');
    ext = ext[ext.length - 1];

    reader.onloadend = () => {
        const img = reader.result.toString().split(',')[1];
        const uploadData = {
            type: ext,
            img
        };

        this.clientServices.uploadAvatarClient(this.client.id, uploadData)
        .subscribe(data => {
          console.log(data);
          Notification.show(data.message, data.status);
          this.client = data.data;
          this.isUploading = false;
        });
    };
    reader.readAsDataURL(file);
  }


  ngOnInit() {
    this.client = JSON.parse(localStorage.getItem('client'));
    this.currentRoute = this.router.url;
  }

  logout() {
    localStorage.removeItem('client');
    this.userServices.logout().subscribe((data) => {
      this.router.navigate(['/sign-in']);
    });
  }

}
