import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

// Layouts
import { AdminLayoutComponent } from '@app/layout/admin-layout/admin-layout.component';
import { DefaultLayoutComponent } from '@app/layout/default-layout/default-layout.component';

// General Pages
import { HomePageComponent } from '@app/pages/home-page/home-page.component';
import { SignUpPageComponent } from '@app/pages/sign-up-page/sign-up-page.component';
import { SignInPageComponent } from '@app/pages/sign-in-page/sign-in-page.component';

import { CatalogPageComponent } from '@app/pages/catalog-page/catalog-page.component';

import { CategoryPageComponent } from '@app/pages/category-page/category-page.component';

import { CartPageComponent } from '@app/pages/cart-page/cart-page.component';

import { ErrorPagesComponent } from '@app/pages/error-pages/error-pages.component';

import { ProductPageComponent } from '@app/pages/product-page/product-page.component';


import { ProductSearchComponent } from '@app/pages/product-search/product-search.component';

// Admin Pages

import { AdminIndexPageComponent } from '@app/pages/admin/admin-index-page/admin-index-page.component';
import { AdminRolesPageComponent } from '@app/pages/admin/admin-roles-page/admin-roles-page.component';

import { AdminStaffsPageComponent } from '@app/pages/admin/admin-staffs-page/admin-staffs-page.component';
import { AdminStaffPageComponent } from '@app/pages/admin/admin-staff-page/admin-staff-page.component';

import { AdminCategoriesPageComponent } from '@app/pages/admin/admin-categories-page/admin-categories-page.component';
import { AdminCategoryPageComponent } from '@app/pages/admin/admin-category-page/admin-category-page.component';

import { AdminCreateProductPageComponent } from '@app/pages/admin/admin-create-product-page/admin-create-product-page.component';
import { AdminProductPageComponent } from '@app/pages/admin/admin-product-page/admin-product-page.component';
import { AdminProductsPageComponent } from '@app/pages/admin/admin-products-page/admin-products-page.component';
import { AdminFiltersPageComponent } from '@app/pages/admin/admin-filters-page/admin-filters-page.component';

// Guards
import { AuthGuard } from '@app/guards/auth.guard';

// Client Account

import { ClientLayoutComponent } from '@app/layout/client-layout/client-layout.component';
import { ClientProfileComponent } from '@app/pages/client/client-profile/client-profile.component';
import { ClientFavouritesComponent } from '@app/pages/client/client-favourites/client-favourites.component';
import { ClientOrdersComponent } from '@app/pages/client/client-orders/client-orders.component';
import { ClientSettingsComponent } from '@app/pages/client/client-settings/client-settings.component';
import { AdminBannersPageComponent } from './pages/admin/admin-banners-page/admin-banners-page.component';



const routes: Routes = [
  {
    path: 'admin',
    component: AdminLayoutComponent,
    children: [
      { path: '', component: AdminIndexPageComponent },
      { path: 'roles', component: AdminRolesPageComponent },
      { path: 'banners', component: AdminBannersPageComponent },
      { path: 'staffs', component: AdminStaffsPageComponent },
      { path: 'staff/:id', component: AdminStaffPageComponent },
      { path: 'filters', component: AdminFiltersPageComponent },
      { path: 'categories', component: AdminCategoriesPageComponent },
      { path: 'category/:id', component: AdminCategoryPageComponent },
      { path: 'products', component: AdminProductsPageComponent },
      { path: 'product/:id', component: AdminProductPageComponent },
      { path: 'create-product', component: AdminCreateProductPageComponent },
      { path: '**', component: ErrorPagesComponent }
    ]
  },
  {
    path: '',
    component: DefaultLayoutComponent,
    children: [
      { path: '', component: HomePageComponent },
      { path: 'catalog', component: CatalogPageComponent },
      { path: 'category/:link', component: CategoryPageComponent },
      { path: 'product/:id/:name', component: ProductPageComponent },
      { path: 'sign-up', component: SignUpPageComponent },
      { path: 'sign-in', component: SignInPageComponent },
      { path: 'search', component: ProductSearchComponent},
      { path: 'cart', canActivate: [AuthGuard],  data: { role: 'client' }, component: CartPageComponent},
      {
        path: 'account',
        component: ClientLayoutComponent,
        canActivate: [AuthGuard],
        data: { role: 'client' },
        children: [
          { path: 'favourites', component: ClientFavouritesComponent },
          { path: 'orders', component: ClientOrdersComponent },
          { path: 'personal', component: ClientProfileComponent },
          { path: 'settings', component: ClientSettingsComponent },
          { path: '', redirectTo: '/account/personal', pathMatch: 'full' },
        ]
      },
      { path: '**', component: ErrorPagesComponent }
    ]
  },
];



@NgModule({
  imports: [RouterModule.forRoot(routes, {scrollPositionRestoration: 'enabled'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
