import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GeneralReturnType } from '@app/class/general';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AdminService {

  constructor(
    private http: HttpClient
  ) { }

  getAdministrators(page: number = 0, limited: number = 0): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/administrator?page=${page}&limited=${limited}`);
  }

  createAdministrators(data: object): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/administrator', data);
  }

  getAdministrator(id: number): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/administrator/${id}`);
  }
}
