import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GeneralReturnType } from '@app/class/general';


@Injectable({
  providedIn: 'root'
})
export class CategoriesService {

  constructor(
    private http: HttpClient
  ) { }

  getCategories(page: number = 0, limited: number = 0): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/category?page=${page}&limited=${limited}`);
  }

  getCategory(id: number): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/category/${id}`);
  }

  getCategoryByParent(id: number = 0): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/category/parent/${id}`);
  }

  getCategoryByLink(link: string): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/category/${link}`);
  }

  getUnassociatedCategories(product: number): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/category/product/${product}`);
  }

  createCategory(data: object): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/category', data);
  }

  deleteCategory(id: number): any {
    return this.http.delete(`/api/category/${id}`);
  }
}
