import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CategoriesService } from '@app/services/categories.service';
import { ProductService } from '@app/services/product.service';

@Component({
  selector: 'app-category-page',
  templateUrl: './category-page.component.html',
  styleUrls: ['./category-page.component.css']
})
export class CategoryPageComponent implements OnInit {
  
  gridView = true;
  link: string;
  category: any;
  products: any;
  queryParams: any;

  constructor(
    private route: ActivatedRoute,
    private categoriesServices: CategoriesService,
    private productServices: ProductService
  ) { }

  ngOnInit() {
    this.route.params.subscribe(
      data => {
        this.link = data.link;
        this.getCategoryByLink(this.link);
      }
    );
  }

  getQueryParams(id: number) {
    this.route.queryParams.subscribe(
      data => {
        this.queryParams = data;
        this.getProductsByCategory();
      }
    );
  }

  getCategoryByLink(link: string) {
    this.categoriesServices.getCategoryByLink(link)
      .subscribe(
        data => {
          this.category = data;
          this.getQueryParams(data.data.id);
      });
  }

  getProductsByCategory() {
    const page = this.queryParams.page ? this.queryParams.page : 0;
    this.productServices.getProductsByCategory(this.category.data.id, page, 1)
    .subscribe(data => {
      this.products = data;
    });
  }

}
