
declare var UIkit: any;
export class Notification {

    static show(message: string, status = 'secondary') {
        const newStatus = status === 'failed' ? 'danger'  : 'success';
        return UIkit.notification(
            {
                message,
                pos: 'bottom-right',
                status: newStatus,
                timeout: 3000
            }
        );
    }
}
