import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, ObservedValueOf } from 'rxjs';
import { GeneralReturnType } from '@app/class/general';

@Injectable({
  providedIn: 'root'
})
export class ProductImagesService {

  constructor(
    private http: HttpClient
  ) { }

  getProductImages(id: number): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/image/${id}`);
  }

  deleteProductImage(id: number): Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/image/${id}`);
  }

  uploadProductImage(data: any): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/image', data);
  }

  setProductMain(id): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/image/${id}`, null);
  }
}
