import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GeneralReturnType, AuthReturnType } from '@app/class/general';
import { Observable } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class UserService {


  constructor(
    private http: HttpClient
  ) { }


  auth(): Observable<AuthReturnType> {
    return this.http.get<AuthReturnType>('/api/auth');
  }

  logout(): Observable<AuthReturnType> {
    return this.http.get<AuthReturnType>('/api/logout');
  }

}
