import { Component, OnInit } from '@angular/core';
import { ProductService } from '@app/services/product.service';
import { Notification } from '@app/utils/notification';
import { DefaultLayoutComponent } from '@app/layout/default-layout/default-layout.component';

@Component({
  selector: 'app-cart-page',
  templateUrl: './cart-page.component.html',
  styleUrls: ['./cart-page.component.css']
})
export class CartPageComponent implements OnInit {

  cart: any;
  client: any;
  cartProducts: any;

  cartItems = 0;
  cartTotal = 0;

  constructor(
    private productServices: ProductService,
    private defaultLayout: DefaultLayoutComponent
    ) {
      this.client = JSON.parse(localStorage.getItem('client'));
    }

  ngOnInit() {
    this.getCartProducts();
  }


  async getCartProducts() {
    this.cart = JSON.parse(localStorage.getItem('cart'));
    const id = this.cart === null || this.cart.id === null ? 0 : this.cart.id;
    this.productServices.getCartProducts(id).subscribe(
      data => {
        this.getCartItemsCount(data);
        this.cartProducts = data;
      }
    );
  }

  getCartItemsCount(cartData: any) {
    let cartCount = 0;
    let cartTotal = 0;
    cartData.data.forEach((value) => {
      cartCount += value.quantity;
      cartTotal += value.price;
    });
    this.cartItems = cartCount;
    this.cartTotal = cartTotal;
  }

  async removeProductFromCart(cartItem: any) {
    this.productServices.removeProductFromCart(cartItem.id).subscribe(
      data => {
        Notification.show(data.message, data.status);
        this.getCartProducts();
      }
    );
  }

  async updateCart(amount: number, cartItem: any) {
    
    const cartData = {
      product: cartItem.product.id,
      client: this.client.id,
      quantity : amount,
      cart: this.cart === null || this.cart.id === null ? '' : this.cart.id
    };
    this.productServices.addProductToCart(cartData).subscribe(
      data => {
        if (data.status === 'success') {
          localStorage.setItem('cart', JSON.stringify(data.data));
        }
        Notification.show(data.message, data.status);
        cartItem.quantity += amount;
        if (cartItem.quantity <= 0) {
          const index = this.cartProducts.data.indexOf(cartItem, 0);
          this.cartProducts.data.splice(index, 1);
        }
        this.defaultLayout.getCartProducts();
        this.getCartProducts();
      }
    );
  }

}
