import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { CategoriesService } from '@app/services/categories.service';
import { Notification } from '@app/utils/notification';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-admin-category-page',
  templateUrl: './admin-category-page.component.html',
  styleUrls: ['./admin-category-page.component.css']
})
export class AdminCategoryPageComponent implements OnInit {

  params: any;

  category: any;

  categoryTitle: string;

  constructor(
    private route: ActivatedRoute,
    private categoriesService: CategoriesService
  ) { }

  onSubmit(form: NgForm) {
    this.createCategory();
    form.form.reset();
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.params = params;
      this.getCategory();
    });
  }

  async getCategory() {
    this.categoriesService.getCategory(this.params.id)
      .subscribe(
          data => this.category = data
        );
  }

  async createCategory() {
    const newCategory = {title: this.categoryTitle, parent: this.category.data.id};
    this.categoriesService.createCategory(newCategory)
      .subscribe(data => {
        Notification.show(data.message, data.status);
        this.getCategory();
      });
  }

}
