import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router, Event as RouterEvent, NavigationCancel, NavigationEnd, NavigationError, NavigationStart } from '@angular/router';
import { CategoriesService } from '@app/services/categories.service';
import { UserService } from '@app/services/user.service';
import { ProductService } from '@app/services/product.service';
import { Notification } from '@app/utils/notification';

declare var UIkit: any;

@Component({
  selector: 'app-default-layout',
  templateUrl: './default-layout.component.html',
  styleUrls: ['./default-layout.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class DefaultLayoutComponent implements OnInit {
  searchValue: string;
  categories: any;
  loading = false;
  cart: any;
  gotCategory = false;

  cartProducts: any;

  cartItems = 0;
  cartTotal = 0;

  constructor(
    private categoriesService: CategoriesService,
    private router: Router,
    private userServices: UserService,
    private productServices: ProductService
  ) {
    this.router.events.subscribe((event: RouterEvent) => {
      switch (true) {
        case event instanceof NavigationStart: {
          this.loading = true;
          break;
        }
        case event instanceof NavigationEnd:
        case event instanceof NavigationCancel:
        case event instanceof NavigationError: {
          this.loading = false;
          break;
        }
        default: {
          break;
        }
      }
    });
  }

  ngOnInit() {
    this.getBaseCategories();
    this.getCartProducts();
  }

  getBaseCategories() {
    this.categoriesService.getCategoryByParent()
      .subscribe(data => {
        this.categories = data;
        this.gotCategory = true;
      });
  }

  logout() {
    localStorage.removeItem('client');
    localStorage.removeItem('admin');
    this.userServices.logout().subscribe((data) => {
      this.router.navigate(['/sign-in']);
    });
  }

  search() {
    this.router.navigate(['/search'], {
      queryParams: {
        title: this.searchValue
      }
    });
  }

  async getCartProducts() {
    this.cart = JSON.parse(localStorage.getItem('cart'));
    const id = this.cart === null || this.cart.id === null ? 0 : this.cart.id;
    this.productServices.getCartProducts(id).subscribe(
      data => {
        this.getCartItemsCount(data);
        this.cartProducts = data;
      }
    );
  }

  async removeProductFromCart(cartItem: any) {
    this.productServices.removeProductFromCart(cartItem.id).subscribe(
      data => {
        Notification.show(data.message, data.status);
        this.getCartProducts();
      }
    );
  }

  getCartItemsCount(cartData: any) {
    let cartCount = 0;
    let cartTotal = 0;
    cartData.data.forEach((value) => {
      cartCount += value.quantity;
      cartTotal += value.price;
    });
    this.cartItems = cartCount;
    this.cartTotal = cartTotal;
  }

  hideOffCanvas(element: string) {
    UIkit.offcanvas(element).hide();
  }

}
