import { Component, OnInit } from '@angular/core';

import { CategoriesService  } from '@app/services/categories.service';
import { NgForm } from '@angular/forms';

import { Notification } from '@app/utils/notification';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-admin-categories-page',
  templateUrl: './admin-categories-page.component.html',
  styleUrls: ['./admin-categories-page.component.css']
})
export class AdminCategoriesPageComponent implements OnInit {

  categories;
  category = {title: '', parent: '' };
  params: any;

  constructor(
    private categoriesService: CategoriesService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  onSubmit(form: NgForm) {
    this.createCategory();
    form.form.reset();
  }

  ngOnInit() {
    this.route.queryParams.subscribe(
      params => {
        this.params = params;
        this.getCategories(params.page ? params.page : 0);
      }
    );
  }

  async getCategories(page) {
    this.categoriesService.getCategories(page, 1)
      .subscribe(data => {
          this.categories = data;
          console.log(data);
        });
  }

  async deleteCategory(id: number) {
    this.categoriesService.deleteCategory(id)
      .subscribe(data => {
        Notification.show(data.message, data.status);
        this.getCategories(this.params.page ? this.params.page : 0);
      });
  }

  async createCategory() {
    this.categoriesService.createCategory(this.category)
      .subscribe(data => {
        Notification.show(data.message, data.status);
        this.getCategories(this.params.page ? this.params.page : 0);
      });
  }

  paginate(move: number) {
    this.router.navigate([], { queryParams: {
      page: (parseInt(this.params.page, 0) || 1) + move
     }});
  }

}
