import { Component, OnInit } from '@angular/core';

import { CategoriesService } from '@app/services/categories.service';
import { ProductService } from '@app/services/product.service';

import { NgForm,  } from '@angular/forms';
import { Router } from '@angular/router';

import { Notification } from '@app/utils/notification';


@Component({
  selector: 'app-admin-create-product-page',
  templateUrl: './admin-create-product-page.component.html',
  styleUrls: ['./admin-create-product-page.component.css']
})

export class AdminCreateProductPageComponent implements OnInit {

  product = {name: '', creator: '', category: '', summary: '', description: '', price: ''};

  categories: any;

  currentGroup = '';

  constructor(
    private categoriesService: CategoriesService,
    private productService: ProductService,
    private router: Router
  ) { }

  onSubmit(form: NgForm) {
    this.createProduct(form);
    console.log(form);
  }

  async createProduct(form: NgForm) {
    this.productService.createProducts(this.product)
    .subscribe(data => {
      Notification.show(data.message, data.status);
      if (data.status === 'success') {
        console.log(data);
        form.form.reset();
        this.router.navigate(['/admin/product', data.data.id]);
      }
      console.log(data);
    });
    console.log('Hello');
  }


  ngOnInit() {
    this.getCategories();
  }

  async getCategories() {
    this.categoriesService.getCategories()
      .subscribe(
        data => {
          this.categories = data;
          if (this.categories.data) {
            const optGroup = {};
            this.categories.data.forEach((category) => {
              const title = category.parent ? category.parent.title : 'Base Category';
              if (!optGroup[title]) {
                optGroup[title] = [];
              }
              optGroup[title].push(category);
            });
            console.log(optGroup);
            this.categories.data = optGroup;
          }

        }
      );
  }

  asIs() { return 0; }

}
