import { Component, OnInit } from '@angular/core';

import { RolesService } from '@app/services/roles.service';

import { Notification } from '@app/utils/notification';

import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-admin-roles-page',
  templateUrl: './admin-roles-page.component.html',
  styleUrls: ['./admin-roles-page.component.css']
})
export class AdminRolesPageComponent implements OnInit {

  roles: {} = {};

  role = { title: '', description: '' };

  onSubmit(form: NgForm) {
    this.createRoles();
    form.form.reset();
  }

  constructor(
    private rolesServices: RolesService,
  ) { }

  ngOnInit() {
    this.getRoles();
  }

  async getRoles() {
    this.rolesServices.getRoles()
      .subscribe(data => this.roles = data);
  }

  async createRoles() {
    this.rolesServices.createRoles(this.role)
      .subscribe(data => {
        Notification.show(data.message , data.status);
        this.getRoles();
      });
  }

  async deleteRoles(id: number) {
    this.rolesServices.deleteRole(id)
      .subscribe(data => {
        Notification.show(data.message , data.status);
        this.getRoles();
      });
    this.getRoles();
  }

}
