import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FilterService } from '@app/services/filter.service';
import { NgForm } from '@angular/forms';
import { ProductService } from '@app/services/product.service';
import { Notification } from '@app/utils/notification';

@Component({
  selector: 'app-product-filter',
  templateUrl: './product-filter.component.html',
  styleUrls: ['./product-filter.component.css']
})
export class ProductFilterComponent implements OnInit {
  @Input() product: any;
  @Output() refreshProduct = new EventEmitter();

  filters: any;
  filter = '';
  filterToAdd: any;

  adding = false;

  constructor(
    private filterService: FilterService,
    private productService: ProductService
  ) { }

  ngOnInit() {
    this.getUnassociatedFilter();
  }

  onSubmit(form: NgForm) {
    this.adding = true;
    this.filterToAdd = form.form.value.filter;
  }

  addFilter(form: NgForm) {
    this.productService.addProductFilter(this.product.id, this.filterToAdd )
      .subscribe(data => {
        Notification.show(data.message, data.status);
        this.stopAdding();
        this.refreshProduct.emit(null);
        this.getUnassociatedFilter();
      });
  }

  stopAdding() {
    this.adding = false;
    this.filterToAdd = '';
    this.filter = '';
  }

  deleteProductFilter(filter: number) {
    this.productService.deleteProductFilter(this.product.id, filter)
      .subscribe(
        data => {
          Notification.show(data.message, data.status);
          this.refreshProduct.emit(null);
          this.getUnassociatedFilter();
        }
      );
  }

  getUnassociatedFilter() {
    this.filterService.getUnassociatedFilters(this.product.id)
      .subscribe(
        data => {
          this.filters = data;
        }
      );
  }


}
