import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GeneralReturnType } from '@app/class/general';

@Injectable({
  providedIn: 'root'
})
export class FilterService {

  constructor(
    private http: HttpClient
  ) { }



  getFilters(page: number = 0, limited: number = 0): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/filter?page=${page}&limited=${limited}`);
  }

  createFilter(data: any): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/filter', data);
  }

  getFilter(id: number): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/filter/${id}`);
  }

  deleteFilter(id: number): Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/filter/${id}`);
  }

  getUnassociatedFilters(product: number): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/filter/product/${product}`);
  }
}
