import { Component, OnInit } from '@angular/core';
import { ProductService } from '@app/services/product.service';
import { BannerImageService } from '@app/services/banner-image.service';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  products: any;
  banners: any;
  params: any;

  title = 'Kizito BookStore';
  description = 'The Oldest bookStore in Abuja';

  constructor(
    private productService: ProductService,
    private bannerImageService: BannerImageService
  ) { }

  ngOnInit() {
    this.getProducts(0);
    this.getBanners();
  }

  getProducts(page: number = 0) {
    this.productService.getProducts(page, 1)
      .subscribe(data => this.products = data);
   }

   getBanners() {
    this.bannerImageService.getActiveBannerImage()
    .subscribe(data => this.banners = data);
   }


}
