import { Component, OnInit } from '@angular/core';
import { ClientService } from '@app/services/client.service';
import { ActivatedRoute, Router, Params } from '@angular/router';

@Component({
  selector: 'app-client-favourites',
  templateUrl: './client-favourites.component.html',
  styleUrls: ['./client-favourites.component.css']
})
export class ClientFavouritesComponent implements OnInit {
  client: any;
  products: any;
  params: Params;

  constructor(
    private clientServices: ClientService,
    private route: ActivatedRoute,
    private router: Router,
  ) {
    this.client = JSON.parse(localStorage.getItem('client'));
  }

  ngOnInit() {
    this.route.queryParams.subscribe(
      data => {
        this.params = data;
        this.getFavourites();
      }
    );
  }

  getFavourites() {
    this.clientServices.getFavourites(this.client.id, this.params).subscribe(
      data => {
        this.products = data;
      }
    );
  }

  paginate(move) {
    const page = (parseInt(this.params.page, 0) || 1) + move;
    console.log(page);
    this.router.navigate([], {
      relativeTo: this.route,
      queryParams: {
        page
      },
      queryParamsHandling: 'merge'
    });
  }


}
