import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { ProductService } from '@app/services/product.service';
import { ProductImagesService } from '@app/services/product-images.service';
import { Notification } from '@app/utils/notification';
import { NgForm } from '@angular/forms';
import { DefaultLayoutComponent } from '@app/layout/default-layout/default-layout.component';


@Component({
  selector: 'app-product-page',
  templateUrl: './product-page.component.html',
  styleUrls: ['./product-page.component.css']
})
export class ProductPageComponent implements OnInit {

  product: any;
  productImages: any;
  params: any;
  productReviews: any;

  cart: any;

  addingToCart = false;


  reviewing = false;
  review = {
    rating: 1,
    review: ''
  };

  quantity = 1;
  client: any;

  constructor(
    private route: ActivatedRoute,
    private productServices: ProductService,
    private productImagesService: ProductImagesService,
    private defaultLayout: DefaultLayoutComponent
  ) {
    this.client = JSON.parse(localStorage.getItem('client'));
  }

  createRange(range): number[] {
    const items: number[] = [];
    for (let i = 1; i <= range; i++) {
      items.push(i);
    }
    return items;
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.params = params;
      this.getProduct();
      this.getProductImages();
    });
  }

  updateRating(rating) {
    this.review.rating = rating;
    console.log(this.review);
  }

  async getProduct() {
    this.productServices.getProduct(this.params.id)
      .subscribe(data => { this.product = data; this.getReviews(); });
  }

  async getReviews() {
    this.productServices.productReviews(this.params.id)
      .subscribe(data => { this.productReviews = data; });
  }

  getProductImages() {
    this.productImagesService.getProductImages(this.params.id).
      subscribe(data => { this.productImages = data; });
  }

  async addToFavourite() {
    if (this.client == null) {
      Notification.show('Login to add to favourite', 'failed');
    } else {
      const favouriteData = {
        client: this.client.id,
        product: this.product.data.id
      };
      this.productServices.addToFavourite(favouriteData).subscribe(
        data => {
          Notification.show(data.message, data.status);
        }
      );
    }
  }

  async reviewProduct(form: NgForm) {
    this.reviewing = true;
    const reviewData = {
      client: this.client.id,
      product: this.product.data.id,
      score: this.review.rating,
      review: this.review.review
    };
    form.form.reset();
    this.productServices.reviewProduct(reviewData).subscribe(
      data => {
        Notification.show(data.message, data.status);
        this.getProduct();
      }
    );
    this.reviewing = false;
  }

  async addToCart() {
    this.setCart();
    if (this.client == null) {
      Notification.show('Login to add to cart', 'failed');
    } else {
      this.addingToCart = true;
      const cartData = {
        product: this.product.data.id,
        client: this.client.id,
        quantity : this.quantity,
        cart: this.cart === null || this.cart.id === null ? '' : this.cart.id
      };
      this.productServices.addProductToCart(cartData).subscribe(
        data => {
          if (data.status === 'success') {
            localStorage.setItem('cart', JSON.stringify(data.data));
          }
          Notification.show(data.message, data.status);
          this.addingToCart = false;
          this.defaultLayout.getCartProducts();
        }
      );
    }
  }

  setCart() {
    this.cart = JSON.parse(localStorage.getItem('cart'));
  }

}
