import { Component, OnInit } from '@angular/core';
import { ClientService } from '@app/services/client.service';
import { NgForm } from '@angular/forms';
import { Notification } from '@app/utils/notification';

@Component({
  selector: 'app-client-settings',
  templateUrl: './client-settings.component.html',
  styleUrls: ['./client-settings.component.css']
})
export class ClientSettingsComponent implements OnInit {
  updatePassword = {
    password: '',
    new_password: '',
    repeat_password: '',
  };
  client: any;

  updatingClient = false;

  constructor(
    private clientService: ClientService
  ) {
    this.client = JSON.parse(localStorage.getItem('client'));
  }

  ngOnInit() {
  }

  onSubmit(form: NgForm) {
    this.updatingClient = true;
    this.updateClient();
    form.form.reset();
  }

  async updateClient() {
    if (this.updatePassword.new_password === this.updatePassword.repeat_password) {
      this.clientService.updateClientPassword(this.client.id, this.updatePassword).subscribe(
        data => {
          this.updatingClient = false;
          Notification.show(data.message, data.status);
        }
      );
    } else {
      this.updatingClient = false;
      Notification.show('Password confirmation faied', 'failed');
    }
  }



}
