import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductService } from '@app/services/product.service';
import { Notification } from '@app/utils/notification';

@Component({
  selector: 'app-admin-products-page',
  templateUrl: './admin-products-page.component.html',
  styleUrls: ['./admin-products-page.component.css']
})
export class AdminProductsPageComponent implements OnInit {

  products: any;
  params: any;

  constructor(
    private productService: ProductService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this.route.queryParams.subscribe(
      params => {
        this.params = params;
        this.getProducts(params.page ? params.page : 0);
      }
    );
  }

  getProducts(page: number = 0) {
    this.productService.getProducts(page, 1)
      .subscribe(data => this.products = data);
   }

  deleteProduct(id: number) {
    if (!confirm('Are you sure')) {
      return;
    }
    this.productService.deleteProduct(id).subscribe(
      data => {
        Notification.show(data.message, data.status);
        this.getProducts(this.params.page);
      }
    );
  }

  paginate(move: number) {
    this.router.navigate([], { queryParams: {
      page: (parseInt(this.params.page, 0) || 1) + move
     }});
  }

}
