import { Component, OnInit } from '@angular/core';

import { RolesService } from '@app/services/roles.service';
import { AdminService } from '@app/services/admin.service';

import { Notification } from '@app/utils/notification';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-admin-staffs-page',
  templateUrl: './admin-staffs-page.component.html',
  styleUrls: ['./admin-staffs-page.component.css']
})
export class AdminStaffsPageComponent implements OnInit {

  admin = {fullname: '', email: '', role: '' };
  roles: any;
  admins: any;

  constructor(
    private rolesService: RolesService,
    private adminService: AdminService
  ) { }

  onSubmit(form: NgForm) {
    this.createAdministrators();
    form.form.reset();
  }

  ngOnInit() {
    this.getRoles();
    this.getAdministrators();
  }

  async getRoles() {
    this.rolesService.getRoles()
      .subscribe(data => this.roles = data);
  }

  async getAdministrators() {
    this.adminService.getAdministrators()
      .subscribe(data => this.admins = data);
  }

  async createAdministrators() {
    this.adminService.createAdministrators(this.admin)
      .subscribe(data => {
        Notification.show(data.message, data.status);
        this.getAdministrators();
      });
  }
}
