import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GeneralReturnType } from '@app/class/general';

@Injectable({
  providedIn: 'root'
})
export class ProductService {

  constructor(
    private http: HttpClient
  ) { }


  getProduct(id): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/product/${id}`);
  }

  deleteProduct(id): Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/product/${id}`);
  }

  getProductsByCategory(id: number, page: number = 0, limited: number = 0): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/product/category/${id}?page=${page}&limited=${limited}`);
  }

  addProductFilter(id, data): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/product/filter/${id}`, data);
  }

  deleteProductFilter(id, filter): Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/product/filter/${id}/${filter}`);
  }

  addProductCategory(id, data): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>(`/api/product/category/${id}`, data);
  }

  deleteProductCategory(id, category): Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/product/category/${id}/${category}`);
  }

  getProducts(page: number = 0, limited: number = 0): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/product?page=${page}&limited=${limited}`);
  }

  findProducts(search: any): Observable<GeneralReturnType> {
    const params = new URLSearchParams(search);
    return this.http.get<GeneralReturnType>(`/api/find_products?${params.toString()}`);
  }
  createProducts(data: any): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/product', data);
  }

  updateProducts(id: number, data: any): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/product/${id}`, data);
  }

  addToFavourite(data: any): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/product/favourite', data);
  }

  removeFromFavourite(productId: number, clientId: number): Observable<GeneralReturnType> {
    return this.http.delete<GeneralReturnType>(`/api/product/favourite/${productId}/${clientId}`);
  }

  reviewProduct(data: any): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/product/review', data);
  }

  productReviews(id: any): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/product/review/${id}`);
  }

  addProductToCart(data: any): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/cart/add', data);
  }

  removeProductFromCart(id: any): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/cart/remove/${id}`);
  }

  getCartProducts(id: any): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/cart/products/${id}`);
  }
}
