import { Component, OnInit } from '@angular/core';
import { FilterService } from '@app/services/filter.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgForm } from '@angular/forms';

import { Notification } from '@app/utils/notification';
import { GeneralReturnType } from '@app/class/general';

@Component({
  selector: 'app-admin-filters-page',
  templateUrl: './admin-filters-page.component.html',
  styleUrls: ['./admin-filters-page.component.css']
})
export class AdminFiltersPageComponent implements OnInit {

  filters: GeneralReturnType;
  filter = { title: '' };
  params: any;


  constructor(
    private filterService: FilterService,
    private route: ActivatedRoute,
    private router: Router
  ) { }


  onSubmit(form: NgForm) {
    this.createFilter();
    form.form.reset();
  }

  ngOnInit() {
    this.route.queryParams.subscribe(
      params => {
        this.params = params;
        this.getFilters(params.page ? params.page : 0);
      }
    );
  }

  async getFilters(page) {
    this.filterService.getFilters(page, 1)
      .subscribe(data => {
          this.filters = data;
          console.log(this.filters);
        });
  }

  async deleteFilter(id: number) {
    this.filterService.deleteFilter(id)
      .subscribe(data => {
        Notification.show(data.message, data.status);
        this.getFilters(this.params.page ? this.params.page : 0);
      });
  }

  async createFilter() {
    this.filterService.createFilter(this.filter)
      .subscribe(data => {
        Notification.show(data.message, data.status);
        this.getFilters(this.params.page ? this.params.page : 0);
      });
  }

  paginate(move: number) {
    this.router.navigate([], { queryParams: {
      page: (parseInt(this.params.page, 0) || 1) + move
     }});
  }

}
