import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { ProductService } from '@app/services/product.service';
import { ProductImagesService } from '@app/services/product-images.service';

import { Notification } from '@app/utils/notification';

@Component({
  selector: 'app-admin-product-page',
  templateUrl: './admin-product-page.component.html',
  styleUrls: ['./admin-product-page.component.css']
})
export class AdminProductPageComponent implements OnInit {

  product: any;
  productImages: any;
  isUploading = false;
  params: any;
  uploadImageTag: any;
  imagesEditable = false;

  constructor(
    private route: ActivatedRoute,
    private productService: ProductService,
    private productImagesService: ProductImagesService,
    private location: Location
  ) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.params = params;
      this.getProduct();
      this.getProductImages();
    });
  }

  async getProduct() {
    this.productService.getProduct(this.params.id)
      .subscribe(data => {this.product = data; } );
  }

  goBack() {
    this.location.back();
  }

  editImages() {
    this.imagesEditable = !this.imagesEditable;
  }

  uploadImageInput(event: any) {
    this.isUploading = true;
    const reader = new FileReader();
    const file = event.target.files[0];
    let ext = file.name.split('.');
    ext = ext[ext.length - 1];

    reader.onloadend = () => {
        const img = reader.result.toString().split(',')[1];
        this.uploadImageTag = reader.result;
        const uploadData = {
            id: this.params.id,
            type: ext,
            img
        };

        this.productImagesService.uploadProductImage(uploadData)
        .subscribe(data => {
          Notification.show(data.message, data.status);
          this.isUploading = false;
          this.getProduct();
          this.getProductImages();
        });
    };
    reader.readAsDataURL(file);
  }

  getProductImages() {
    this.productImagesService.getProductImages(this.params.id).
    subscribe( data => this.productImages = data);
  }


  deleteProductImage(productImage: number) {
    this.productImagesService.deleteProductImage(productImage)
      .subscribe( data => {
        Notification.show(data.message, data.status);
        this.getProduct();
        this.getProductImages();
      });
  }

  setProductMainImage(productImage: number) {
    this.productImagesService.setProductMain(productImage)
      .subscribe( data => {
        Notification.show(data.message, data.status);
        this.getProduct();
        this.getProductImages();
      });
  }

}
