import { Component, OnInit } from '@angular/core';
import { ClientService } from '@app/services/client.service';
import { NgForm } from '@angular/forms';
import { Notification } from '@app/utils/notification';

@Component({
  selector: 'app-client-profile',
  templateUrl: './client-profile.component.html',
  styleUrls: ['./client-profile.component.css']
})
export class ClientProfileComponent implements OnInit {
  client: any;
  updatingClient = false;
  constructor(
    private clientServices: ClientService
  ) {
    this.client = JSON.parse(localStorage.getItem('client'));
  }

  ngOnInit() {
  }

  async getClient() {
    this.clientServices.getClient(this.client.id).subscribe(
      data => {
        if (data.status === 'success') {
          this.client = data.data;
        }
      }
    );
  }

  onSubmit(form: NgForm) {
    this.updatingClient = true;
    this.updateClient();
  }

  async updateClient() {
    this.clientServices.updateClient(this.client.id, this.client).subscribe(
      data => {
        this.updatingClient = false;
        Notification.show(data.message, data.status);
        if (data.status === 'success') {
          localStorage.setItem('client', JSON.stringify(data.data));
        }
      }
    );
  }





}
