import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { GeneralReturnType } from '@app/class/general';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ClientService {

  constructor(
    private http: HttpClient
  ) { }

  // getClients(page: number = 0, limited: number = 0): Observable<GeneralReturnType> {
  //   return this.http.get<GeneralReturnType>(`/api/administrator?page=${page}&limited=${limited}`);
  // }

  createClient(data: object): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/client', data);
  }

  loginClient(data: object): Observable<GeneralReturnType> {
    return this.http.post<GeneralReturnType>('/api/client/login', data);
  }

  getFavourites(client: number, search?: any): Observable<GeneralReturnType> {
    const params = new URLSearchParams(search);
    return this.http.get<GeneralReturnType>(`/api/client/favourite/${client}?${params}`);
  }

  getClient(client: number): Observable<GeneralReturnType> {
    return this.http.get<GeneralReturnType>(`/api/client/${client}`);
  }

  updateClient(client: number, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/client/${client}`, data);
  }

  uploadAvatarClient(client: number, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/client/${client}/avatar`, data);
  }

  updateClientPassword(client: number, data): Observable<GeneralReturnType> {
    return this.http.put<GeneralReturnType>(`/api/client/change_password/${client}`, data);
  }

  // getAdministrator(id: number): Observable<GeneralReturnType> {
  //   return this.http.get<GeneralReturnType>(`/api/administrator/${id}`);
  // }
}
